











import Vue from "vue";
import {Component, Prop} from "vue-property-decorator";

@Component
export default class PayTags extends Vue {
  selectorTags: string[] = [];
  @Prop() payDataSource: string[] | undefined;

  toggle(tag: string) {
    const index = this.selectorTags.indexOf(tag);
    if (index >= 0) {
      this.selectorTags.splice(index, 1);
    } else {
      this.selectorTags.push(tag);
    }
    this.$emit("update:value", this.selectorTags);
  }
}
