






















import Vue from "vue";
import {Component, Prop} from "vue-property-decorator";

@Component
export default class Tags extends Vue {

  addSource: string[] = [];
  selectorTags: string[] = [];
  @Prop() dataSource: string[] | undefined;

  toggle(tag: string) {
    const index = this.selectorTags.indexOf(tag);
    if (this.selectorTags.length === 1) {
      this.selectorTags.splice(index, 1);
      this.selectorTags.push(tag);
    } else {
      this.selectorTags.push(tag);
    }
    this.$emit("update:value", this.selectorTags);
  }

  add() {
    const name = window.prompt("请输入标签名（四个字以内）：");
    if (name === "") {
      alert("名字为空");
    } else if (this.addSource.indexOf(name as string) >= 0) {
      alert("名字已存在");
    } else if (name == null) {
      return;
    } else {this.addSource.push(name as string);}

  }


}
