











import Vue from "vue";
import {Component} from "vue-property-decorator";
import Bar from "@/components/Money/bar.vue";
import Tags from "@/components/Money/tags.vue";
import FormItem from "@/components/Money/FormItem.vue";
import NumberPad from "@/components/Money/NumberPad.vue";
import PayTags from "@/components/Money/PayTags.vue";
import dayjs from "dayjs";

@Component({
      components: {PayTags, NumberPad, FormItem, Tags, Bar},
    }
)
export default class Money extends Vue {
  tags = ["餐饮", "交通", "教育", "医疗", "娱乐", "宠物", "电费", "通讯", "美妆", "住房", "物业", "礼金"];
  payTags = ["工资", "理财", "红包", "报销",];
  record: RecordItem = {type: "-", tag: [], note: "", amount: 0, creatTime: dayjs(new Date()).toISOString()};


  get recordList() {
    return this.$store.state.recordList;
  }

  created() {
    this.$store.commit("fetchRecord");

  }

  onUpdateTag(value: string[]) {
    this.record.tag = value;
  }

  onUpdateAmount(value: string) {
    this.record.amount = parseFloat(value);
  }

  saveRecord() {
    if(this.record.tag.length === 0){
      alert('请选择标签，无标签请新建标签~')
    }else{
      this.$store.commit("createRecord", this.record);
      this.$router.replace('/statistics')
    }
  }
}
