










































import Vue from "vue";
import {Component, Watch} from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import echarts from "echarts";
import dayjs from "dayjs";
import clone from "@/lib/clone";

import Icon from "@/components/Icon.vue";
import twoNumber from "@/lib/twoNumber";


@Component({components: {Tabs, Icon}})
export default class Labels extends Vue {
  type = "-";
  typeGroup = [{text: "支出", value: "-"}, {text: "收入", value: "+"}];
  interval: "week" | "month" | "year" = "month";
  intervalList: tabItem[] = [{text: "本周", value: "week"}, {text: "本月", value: "month"}, {text: "本年", value: "year"}];


  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get targetRecords() {
    return clone(this.recordList).filter(r => r.type === this.type)
        .filter(r => dayjs(r.creatTime).isSame(dayjs(), this.interval));
  }

  get day() {
    const [year, month] = [dayjs().year(), dayjs().month()];
    const d = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
    if ((year % 4 === 0 && year % 100 != 0) || (year % 100 === 0 && year % 400 === 0)) {
      if (month === 1) { return 29;} else {
        return d[month];
      }
    } else {
      return d[month];
    }
  }

  get total() {
    const total = [...this.intervalGroup().values()];
    let sum = 0;
    for (let i = 0; i < total.length; i++) {
      sum += total[i];
    }
    return sum;
  }

  rankList() {

    type Group = { name: string; amounts: number; rank: number }
    let list: Group[] = [];
    const tags: string[] = [];
    let r: RecordItem;
    for (r of this.targetRecords) {
      const index = tags.indexOf(r.tag[0]);
      if (index < 0) {
        tags.push(r.tag[0]);
        list.push({name: r.tag[0], amounts: r.amount, rank: 0});
      } else {
        list[index].amounts += r.amount;
      }
    }
    for (let i = 0; i < list.length; i++) {
      list[i].rank = twoNumber((list[i].amounts / this.total)*100);
    }
    list = list.sort((a, b) => (b.rank - a.rank));
    return list;
  }

  weekResult(records: RecordItem[]): Map<string, number> {
    const keys = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
    const result = new Map<string, number>();
    for (let i = 0; i < keys.length; i++) {
      result.set(keys[i], 0);
    }
    let r: RecordItem;
    for (r of records) {
      const day = keys[dayjs(r.creatTime).day()];
      //得到记录时间是周几
      const amount = result.get(day) as number;
      result.set(day, amount + r.amount);
    }
    return result;
  }

  monthResult(records: RecordItem[]): Map<string, number> {
    const keys: string[] = [];
    const result = new Map<string, number>();
    let i: number;
    for (i = 1; i <= this.day; i++) {
      keys.push(i.toString());
    }
    for (i = 0; i < keys.length; i++) {
      result.set(keys[i], 0);
    }
    let r: RecordItem;
    for (r of records) {
      const key = dayjs(r.creatTime).date().toString();
      const amount = result.get(key) as number;
      result.set(key, amount + r.amount);
    }
    return result;

  }

  yearResult(records: RecordItem[]): Map<string, number> {
    const keys = ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"];
    const result = new Map();
    for (let i = 0; i < keys.length; i++) {
      result.set(keys[i], 0);
    }
    let r: RecordItem;
    for (r of records) {
      const year = keys[dayjs(r.creatTime).month()];
      const amount = result.get(year) as number;
      result.set(year, amount + r.amount);
    }
    return result;
  }

  intervalGroup() {
    let result = new Map<string, number>();
    switch (this.interval) {
      case "week":
        result = this.weekResult(this.targetRecords);
        // eslint-disable-next-line no-unreachable
        break;
      case "month":
        result = this.monthResult(this.targetRecords);
        // eslint-disable-next-line no-unreachable
        break;
      case "year":
        result = this.yearResult(this.targetRecords);
        // eslint-disable-next-line no-unreachable
        break;
    }
    return result;
  }

  toArray(value: number, length: number): number[] {
    const result: number[] = [];
    for (let i = 0; i < length; i++) {
      result.push(value);
    }
    return result;
  }

  draw(data: Map<string, number>) {
    const keys = [...data.keys()];
    const values = [...data.values()];
    const figure = echarts.init(document.getElementById("figure") as HTMLDivElement);
    console.log(document.getElementById("figure"));
    figure.setOption({
      grid: {
        top: 10,
        bottom: 30,
        left: 10,
        right: 10
      },
      xAxis: {
        data: keys,
        axisTick: {
          interval: 0,
          lineStyle: {
            opacity: 0
          }
        },
        axisLabel: {
          interval: 0,
          fontSize: 10,
          color: "#999999"
        }
      },
      yAxis: {
        axisLine: {
          lineStyle: {
            opacity: 0
          }
        },
        splitLine: {
          lineStyle: {
            opacity: 0
          }
        },
        axisLabel: undefined,
        axisTick: undefined,
      },
      series: [
        {
          type: "line",
          data: values
        },
        {
          name: "最大值",
          type: "line",
          data: this.toArray(Math.max(...values), values.length),
          symbol: "none",
          lineStyle: {
            color: "#999999",
            width: 1,
            opacity: 0.5
          }
        }]
    });

    console.log(keys);
    console.log(values);
    console.log(figure);
  }

  mounted() {
    this.draw(this.intervalGroup());
  }

  beforeCreate() {this.$store.commit("fetchRecord");}

  @Watch("type")
  onTypeChange() {
    this.draw(this.intervalGroup());
  }

  @Watch("interval")
  onIntervalChange() {
    this.draw(this.intervalGroup());
  }

}
