


































































import Vue from "vue";
import {Component, Watch} from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import dayjs from "dayjs";
import clone from "@/lib/clone";
import Icon from "@/components/Icon.vue";


@Component({components: {Tabs, Icon}})
export default class Statistics extends Vue {
  year = window.sessionStorage.getItem("year") || dayjs().year().toString();
  month = window.sessionStorage.getItem("month") || (dayjs().month() + 1).toString();

  @Watch("year")
  saveYear(year: string) {
    window.sessionStorage.setItem("year", year);
  }

  @Watch("month")
  saveMonth(month: string) {
    window.sessionStorage.setItem("month", month);
  }

  beautifyMonth(month: number) {
    return month < 10 ? "0" + month.toString() : month.toString();
  }

  beforeCreate() {this.$store.commit("fetchRecord");}

  get years() {
    const endYear = dayjs().year();
    let y = 1970;
    const result: number[] = [];
    while (endYear >= y) {
      result.push(y);
      y++;
    }
    return result;
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {
    const {recordList} = this;

    if (recordList.length === 0) {return [];}
    const newList = clone(recordList)
        .filter(r => (dayjs(r.creatTime).year() === parseInt(this.year)) && (dayjs(r.creatTime).month() + 1 === parseInt(this.month)))
        .sort((a, b) => dayjs(b.creatTime).valueOf() - dayjs(a.creatTime).valueOf());
    if (newList.length === 0) {return [] as result[];}
    const hashTableList: result[] = [{
      title: dayjs(newList[0].creatTime).format("YYYY-MM-DD"),
      items: [newList[0]]
    }];
    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = hashTableList[hashTableList.length - 1];
      if (dayjs(current.creatTime).isSame(last.title, "day")) {
        last.items.push(current);
      } else {
        hashTableList.push({title: dayjs(current.creatTime).format("YYYY-MM-DD"), items: [current]});
      }
    }
    console.log(hashTableList);
    console.log(this.month);
    return hashTableList;
  }

  get incomeTotal() {
    let total = 0;
    let group: result;
    for (group of this.groupedList) {
      let item: RecordItem;
      for (item of group.items) {
        if (item.type === "-") {
          total += item.amount;
        } else {
          continue;
        }
      }
    }
    return total;
  }

  get payTotal() {
    let total = 0;
    let group: result;
    for (group of this.groupedList) {
      let item: RecordItem;
      for (item of group.items) {
        if (item.type === "+") {
          total += item.amount;
        } else {
          continue;
        }
      }
    }
    return total;
  }

  dayIncome(group: result) {
    let income = 0;
    let item: RecordItem;
    for (item of group.items) {
      if (item.type === "-") {
        income += item.amount;
      }
    }
    return income;
  }


  dayPay(group: result) {
    let pay = 0;
    let item: RecordItem;
    for (item of group.items) {
      if (item.type === "+") {
        pay += item.amount;
      }
    }
    return pay;
  }

  tagString(tags: string[]) {
    return tags.length === 0 ? "无标签" : tags.join(",");
  }

  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs();
    if (day.isSame(now, "day")) {
      return "今天";
    } else if (day.isSame(now.subtract(1, "day"), "day")) {
      return "昨天";
    } else if (day.isSame(now.subtract(2, "day"), "day")) {
      return "前天";
    } else if (day.isSame(now, "year")) {
      return day.format("MM年DD日");
    } else {
      return day.format("YYYY年MM月DD日");
    }
  }


}
